import axios from "@/utils/request"
import path from "@/api/path"

const api = {
    getChengpin(){
        return axios.get(path.chengpin);
    },
    getToken(){
        return axios.get(path.get_token);
    },
    submitBlog(params: object) {
        return axios.post(path.submit_blog, params);
    }
}

export default api